'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import socket from '@/lib/socket';
import { Booking } from '@/types';

export function useBookingAdminPage() {
    // Data State
    const [bookings, setBookings] = useState<Booking[]>([]);
    const [loading, setLoading] = useState(true);

    // Filter State
    const [searchQuery, setSearchQuery] = useState('');
    const [filterStatus, setFilterStatus] = useState('all');
    const [selectedDateFilter, setSelectedDateFilter] = useState<string | null>(null);

    // Pagination State
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(10);

    // Dialog State
    const [selectedBooking, setSelectedBooking] = useState<Booking | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);

    // Fetch Data
    const fetchBookings = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/bookings');
            setBookings(response.data);
        } catch (error) {
            toast.error("Gagal memuat data booking.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchBookings();
        // Optional: Socket listener setup here
    }, []);

    // Filter Logic
    const filteredBookings = bookings.filter(item => {
        const matchSearch = item.kode_booking.toLowerCase().includes(searchQuery.toLowerCase()) || 
                            item.user?.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                            (item.nama_pengirim && item.nama_pengirim.toLowerCase().includes(searchQuery.toLowerCase()));
        
        const matchStatus = filterStatus === 'all' || item.status_booking_id.toString() === filterStatus;
        
        // Date filter: compare YYYY-MM-DD
        const itemDate = new Date(item.tanggal_booking).toISOString().split('T')[0];
        const filterDate = selectedDateFilter ? new Date(selectedDateFilter).toISOString().split('T')[0] : null;
        const matchDate = !filterDate || itemDate === filterDate;
        
        return matchSearch && matchStatus && matchDate;
    });

    // Pagination Logic
    const totalData = filteredBookings.length;
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredBookings.slice(indexOfFirstItem, indexOfLastItem);

    // Handlers
    const handlePageChange = (page: number) => {
        setCurrentPage(page);
    };

    const handleViewDetail = (booking: Booking) => {
        setSelectedBooking(booking);
        setIsDialogOpen(true);
    };

    const handleApprove = async (id: number) => {
        if(!confirm("Konfirmasi booking ini?")) return;
        setIsProcessing(true);
        try {
            const response = await axios.patch(`/api/bookings/${id}/status`, { status_booking_id: 3 });
            toast.success("Booking berhasil dikonfirmasi!");
            try { socket.emit('booking:confirmed', response.data); } catch(e) {}
            setIsDialogOpen(false);
            fetchBookings();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal konfirmasi.");
        } finally {
            setIsProcessing(false);
        }
    };

    const handleReject = async (id: number) => {
        if(!confirm("Tolak booking ini?")) return;
        setIsProcessing(true);
        try {
            const response = await axios.patch(`/api/bookings/${id}/cancel`); 
            toast.success("Booking dibatalkan.");
            try { socket.emit('booking:rejected', response.data); } catch(e) {}
            setIsDialogOpen(false);
            fetchBookings();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal membatalkan.");
        } finally {
            setIsProcessing(false);
        }
    };

    return {
        // Data
        bookings: currentItems, // Return data yang sudah dipaginasi
        totalData,
        loading,
        
        // Filter States & Setters
        searchQuery, setSearchQuery,
        filterStatus, setFilterStatus,
        selectedDateFilter, setSelectedDateFilter,

        // Pagination States & Handlers
        currentPage,
        itemsPerPage,
        totalPages,
        handlePageChange,

        // Dialog & Actions
        selectedBooking,
        isDialogOpen, setIsDialogOpen,
        isProcessing,
        handleViewDetail,
        handleApprove,
        handleReject,
        fetchBookings
    };
}