'use client';

import { Search, X, Filter } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";

interface BookingFiltersProps {
    searchQuery: string;
    setSearchQuery: (value: string) => void;
    filterStatus: string;
    setFilterStatus: (value: string) => void;
}

export default function BookingFilters({
    searchQuery,
    setSearchQuery,
    filterStatus,
    setFilterStatus,
}: BookingFiltersProps) {
    return (
        <div className="flex flex-col md:flex-row gap-4 p-4 border border-gray-200 rounded-lg shadow-sm bg-white">
            <div className="flex-1 relative">
                <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                <Input
                    placeholder="Cari kode booking, nama pemesan..."
                    className="pl-9"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
            </div>
            
            <div className="w-full md:w-[200px]">
                <Select value={filterStatus} onValueChange={setFilterStatus}>
                    <SelectTrigger>
                        <Filter className="w-4 h-4 mr-2 text-gray-500" />
                        <SelectValue placeholder="Status" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">Semua Status</SelectItem>
                        <SelectItem value="1">Menunggu Bayar</SelectItem>
                        <SelectItem value="2">Perlu Konfirmasi</SelectItem>
                        <SelectItem value="3">Terkonfirmasi</SelectItem>
                        <SelectItem value="4">Dibatalkan</SelectItem>
                    </SelectContent>
                </Select>
            </div>

            {(searchQuery || filterStatus !== 'all') && (
                <Button variant="ghost" onClick={() => { setSearchQuery(''); setFilterStatus('all'); }} className="text-gray-500">
                    <X className="h-4 w-4 mr-2" /> Reset
                </Button>
            )}
        </div>
    );
}