'use client';

import Link from 'next/link';
import { Plus, RefreshCcw, ChevronLeft, ChevronRight } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';

// Custom Hooks & Components
import { useBookingAdminPage } from '@/hooks/admin/useBookingAdminPage';
import BookingFilters from '@/components/admin/bookings/BookingFilters';
import BookingTable from '@/components/admin/bookings/BookingTable';
import BookingDetailDialog from '@/components/admin/bookings/BookingDetailDialog';
import BookingCalendar from '@/components/admin/bookings/BookingCalendar';

export default function BookingAdminPage() {
    const {
        // Data
        bookings,
        loading,
        totalData,

        // Filters
        searchQuery, setSearchQuery,
        filterStatus, setFilterStatus,
        selectedDateFilter, setSelectedDateFilter,

        // Pagination
        currentPage,
        totalPages,
        handlePageChange,

        // Dialog & Actions
        selectedBooking,
        isDialogOpen, setIsDialogOpen,
        isProcessing,
        handleViewDetail,
        handleApprove,
        handleReject,
        fetchBookings
    } = useBookingAdminPage();

    return (
        <div className="space-y-6 pb-20">
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Manajemen Booking</h1>
                    <p className="text-gray-500 text-sm">Validasi pembayaran dan kelola jadwal lapangan.</p>
                </div>
                <div className="flex gap-2">
                    <Button variant="outline" onClick={fetchBookings} title="Refresh Data" disabled={loading}>
                        <RefreshCcw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
                    </Button>
                    <Link href="/admin/bookings/create">
                        <Button className="bg-[#D93F21] hover:bg-[#b9351b]">
                            <Plus className="w-4 h-4 mr-2" /> Input Manual
                        </Button>
                    </Link>
                </div>
            </div>

            {/* Filters Section */}
            <BookingFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterStatus={filterStatus}
                setFilterStatus={setFilterStatus}
            />

            {/* Calendar & Date Filter Info */}
            <div className="mt-4 space-y-3">
                <BookingCalendar 
                    bookings={bookings} 
                    onSelectDate={setSelectedDateFilter} 
                />

                {selectedDateFilter && (
                    <div className="flex items-center justify-between bg-white p-3 rounded-md border shadow-sm">
                        <div className="text-sm">
                            Filter tanggal: <span className="font-medium text-[#D93F21]">{new Date(selectedDateFilter).toLocaleDateString('id-ID', { dateStyle: 'full' })}</span>
                        </div>
                        <button onClick={() => setSelectedDateFilter(null)} className="text-xs text-gray-500 hover:text-red-600 underline">
                            Hapus filter
                        </button>
                    </div>
                )}
            </div>

            {/* Table Section */}
            <Card className="border shadow-sm overflow-hidden">
                <BookingTable 
                    bookings={bookings}
                    loading={loading}
                    onView={handleViewDetail}
                />
                
                {/* Pagination Controls */}
                {!loading && totalData > 0 && (
                    <div className="flex items-center justify-between p-4 border-t bg-gray-50/50">
                        <div className="text-sm text-gray-500">
                            Menampilkan <strong>{bookings.length}</strong> dari <strong>{totalData}</strong> data
                        </div>
                        <div className="flex items-center gap-2">
                            <Button
                                variant="outline"
                                size="sm"
                                onClick={() => handlePageChange(currentPage - 1)}
                                disabled={currentPage === 1}
                                className="h-8 w-8 p-0"
                            >
                                <ChevronLeft className="h-4 w-4" />
                            </Button>
                            <span className="text-sm font-medium px-2">
                                Hal {currentPage} / {totalPages}
                            </span>
                            <Button
                                variant="outline"
                                size="sm"
                                onClick={() => handlePageChange(currentPage + 1)}
                                disabled={currentPage === totalPages}
                                className="h-8 w-8 p-0"
                            >
                                <ChevronRight className="h-4 w-4" />
                            </Button>
                        </div>
                    </div>
                )}
            </Card>

            {/* Detail Dialog */}
            <BookingDetailDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                booking={selectedBooking}
                onApprove={handleApprove}
                onReject={handleReject}
                isProcessing={isProcessing}
                onRefresh={fetchBookings}
            />
        </div>
    );
}