'use client';

import { useState } from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import { Booking } from '@/types';

interface BookingCalendarProps {
    bookings: Booking[];
    onSelectDate?: (dateStr: string | null) => void; // Emit selected date to parent (format YYYY-MM-DD)
}

export default function BookingCalendar({ bookings, onSelectDate }: BookingCalendarProps) {
    const [currentDate, setCurrentDate] = useState(new Date());
    const [selectedDate, setSelectedDate] = useState<string | null>(null);

    const getDaysInMonth = (date: Date) => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const daysInMonth = new Date(year, month + 1, 0).getDate();
        const firstDay = new Date(year, month, 1).getDay();
        return { daysInMonth, firstDay };
    };

    const { daysInMonth, firstDay } = getDaysInMonth(currentDate);

    const prevMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1, 1));
    const nextMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 1));

    const formatDateKey = (year: number, month: number, day: number) => {
        return `${year}-${String(month + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
    };

    const isBooked = (day: number) => {
        const dateStr = formatDateKey(currentDate.getFullYear(), currentDate.getMonth(), day);
        return bookings.some(b => b.tanggal_booking === dateStr && b.status_booking?.nama_status !== 'Dibatalkan');
    };

    const handleDayClick = (day: number) => {
        const dateStr = formatDateKey(currentDate.getFullYear(), currentDate.getMonth(), day);
        const booked = bookings.some(b => b.tanggal_booking === dateStr && b.status_booking?.nama_status !== 'Dibatalkan');
        setSelectedDate(dateStr);
        if (onSelectDate) onSelectDate(dateStr);
    };

    const clearSelection = () => {
        setSelectedDate(null);
        if (onSelectDate) onSelectDate(null);
    };

    const bookingsForDate = selectedDate ? bookings.filter(b => b.tanggal_booking === selectedDate && b.status_booking?.nama_status !== 'Dibatalkan') : [];

    return (
        <div className="bg-white rounded-md border p-4 shadow-sm">
            <div className="flex items-center justify-between mb-4">
                <div className="flex items-center gap-2">
                    <button onClick={prevMonth} className="p-2 hover:bg-gray-50 rounded-md border border-gray-200">
                        <ChevronLeft className="w-4 h-4" />
                    </button>
                    <h3 className="text-lg font-semibold">
                        {currentDate.toLocaleDateString('id-ID', { month: 'long', year: 'numeric' })}
                    </h3>
                    <button onClick={nextMonth} className="p-2 hover:bg-gray-50 rounded-md border border-gray-200">
                        <ChevronRight className="w-4 h-4" />
                    </button>
                </div>
                <div className="text-sm text-gray-500"> <span className="text-red-500 font-medium">Terbooking</span></div>
            </div>

            <div className="grid grid-cols-7 gap-1 text-xs text-gray-500 mb-2">
                {['Min','Sen','Sel','Rab','Kam','Jum','Sab'].map(d => (
                    <div key={d} className="text-center font-medium">{d}</div>
                ))}
            </div>

            <div className="grid grid-cols-7 gap-2">
                {Array.from({ length: firstDay }).map((_, i) => (
                    <div key={`empty-${i}`} className="h-10"></div>
                ))}

                {Array.from({ length: daysInMonth }).map((_, i) => {
                    const day = i + 1;
                    const dateStr = formatDateKey(currentDate.getFullYear(), currentDate.getMonth(), day);
                    const booked = isBooked(day);
                    const isSelected = selectedDate === dateStr;

                    return (
                        <button key={day} onClick={() => handleDayClick(day)} className={`h-10 rounded-md flex items-center justify-center text-sm transition-shadow ${isSelected ? 'bg-[#D93F21] text-white shadow-md' : booked ? 'bg-red-50 text-red-600 border border-red-100' : 'bg-white text-gray-800 border border-gray-100'}`}>
                            <div className="relative w-full h-full flex items-center justify-center">
                                <span className={`pointer-events-none ${isSelected ? 'font-bold' : ''}`}>{day}</span>
                                {booked && (
                                    <span className="absolute -bottom-1 w-2 h-2 bg-red-500 rounded-full" />
                                )}
                            </div>
                        </button>
                    );
                })}
            </div>

            {/* Selected date panel */}
            <div className="mt-4">
                {selectedDate ? (
                    <div className="p-3 rounded-md border bg-gray-50">
                        <div className="flex items-start justify-between mb-2">
                            <div>
                                <div className="text-xs text-gray-500">Tanggal dipilih</div>
                                <div className="font-medium">{new Date(selectedDate).toLocaleDateString('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' })}</div>
                            </div>
                            <div className="text-sm">
                                <button onClick={clearSelection} className="text-sm text-gray-500 hover:text-gray-700">Bersihkan</button>
                            </div>
                        </div>

                        {bookingsForDate.length === 0 ? (
                            <div className="text-sm text-gray-500">Tidak ada booking untuk tanggal ini.</div>
                        ) : (
                            <div className="space-y-2">
                                {bookingsForDate.map(b => (
                                    <div key={b.id} className="p-2 rounded-md border bg-white flex items-center justify-between">
                                        <div>
                                            <div className="font-medium">{b.jam_mulai} - {b.jam_selesai} &middot; {b.kode_booking}</div>
                                            <div className="text-xs text-gray-500">{b.user?.name}  {b.lapangan?.nama_lapangan}</div>
                                        </div>
                                        <div className="text-xs text-right text-gray-600">{b.status_booking?.nama_status}</div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                ) : (
                    <div className="text-sm text-gray-500">Klik tanggal untuk melihat jam yang sudah terbooking.</div>
                )}
            </div>
        </div>
    );
}
